@keyframes pulse{0%,100%{background-color:#172737}50%{background-color:#17314c}}.breadcrumbs{display:flex;flex-direction:row;margin-bottom:10px}.breadcrumbsItems{list-style:none;padding:0;display:flex;flex-direction:row;align-items:baseline}.breadcrumbsItem{position:relative;margin-right:30px;color:#8a98a5;font-size:11px;font-weight:500}.breadcrumbsItem:after{content:"";color:rgba(255,255,255,.3);position:absolute;right:-15px;top:50%;transform:translate(50%, -50%);font-size:18px;opacity:1}.breadcrumbsItem:last-child:after{display:none}.breadcrumbsItem a{opacity:.7;transition:opacity .3s ease-in-out}.breadcrumbsItem a:hover{opacity:1}.breadcrumbsItem.breadcrumbsHome{color:#6aded4;font-size:15px;opacity:1}.breadcrumbsItem.breadcrumbsHome a{display:block;width:24px;height:24px;opacity:1}.breadcrumbsItem.breadcrumbsHome a svg{vertical-align:middle}@media(min-width: 768px){.breadcrumbs{margin-bottom:25px}.breadcrumbsItem{font-size:12px;margin-right:40px}.breadcrumbsItem:after{right:-20px}.breadcrumbsItem.breadcrumbsHome{font-size:16px}}@media(min-width: 1280px){.breadcrumbs{margin-bottom:25px}.breadcrumbsItem{font-size:13px}.breadcrumbsItem.breadcrumbsHome{font-size:17px}}@media(min-width: 1700px){.breadcrumbs{margin-bottom:25px}}